import React, { useRef, useCallback, useEffect, useState } from "react";
import Vertex from "../shader/test/Vertex.glsl";
import * as THREE from "three";

import gsap from "gsap";

import scene from "../three/scene";
import camera from "../three/camera";
import gui from "../three/gui";
import renderer from "../three/renderer";
import axesHelper from "../three/axesHelper";
import controls from "../three/controls";
import "../three/init";
import animate from "../three/animate";
import createMesh from "../three/createMesh";
import AlarmSprite from "../three/mesh/AlarmSprite";
import LightWall from "../three/mesh/LightWall";
import FlyLineShader from "../three/mesh/FlyLineShader";
import LightRadar from "../three/mesh/LightRadar";
import FlyLine from "../three/mesh/FlyLine";
import eventHub from "../utils/eventHub";
export default function Scene(props) {
  const sceneDiv = useRef();
  const { eventList } = props;
  //添加相机到场景
  scene.add(camera);

  const eventListMesh = useRef([]);

  eventListMesh.current.forEach((item) => item.remove());
  const mapFn = {
    火警: (position, i) => {
      const lightWall = new LightWall(1, 2, position);
      lightWall.eventListIndex = i;
      scene.add(lightWall.mesh);
      eventListMesh.current.push(lightWall);
    },
    治安: (position, i) => {
      const color = new THREE.Color(Math.random(), Math.random(), Math.random()).getHex();
      const flyLineShader = new FlyLineShader(position, color);
      flyLineShader.eventListIndex = i;
      scene.add(flyLineShader.mesh);
      eventListMesh.current.push(flyLineShader);
    },
    电力: (position, i) => {
      const color = new THREE.Color(Math.random(), Math.random(), Math.random()).getHex();
      const lightRadar = new LightRadar(4, position, color);
      lightRadar.eventListIndex = i;
      scene.add(lightRadar.mesh);
      const flyLine = new FlyLine(position);
      flyLine.eventListIndex = i;
      scene.add(flyLine.mesh);
      eventListMesh.current.push(lightRadar);
      eventListMesh.current.push(flyLine);
    },
  };
  eventList.forEach((item, i) => {
    // 添加标志
    const position = {
      x: item.position.x / 5,
      z: item.position.y / 5,
    };
    const alarmSprite = new AlarmSprite(item.name, position);
    eventListMesh.current.push(alarmSprite);
    scene.add(alarmSprite.mesh);
    alarmSprite.onClick(function (e) {
      // alert("这栋楼电力出现故障");
      eventHub.emit("spriteClick", { event: item, i });
    });
    alarmSprite.eventListIndex = i;
    if (mapFn[item.name]) {
      mapFn[item.name](position, i);
    }
  });

  // scene.add(axesHelper);

  useEffect(() => {
    sceneDiv.current.appendChild(renderer.domElement);
    createMesh();
    animate();

    eventHub.on("eventToggle", (i) => {
      eventListMesh.current.forEach((item) => {
        if (item.eventListIndex === i) {
          item.mesh.visible = true;
        } else {
          item.mesh.visible = false;
        }
      });
      const position = {
        x: eventList[i].position.x / 5,
        y: 0,
        z: eventList[i].position.y / 5 - 20,
      };
      // controls.target.set(position.x, position.y, position.z);
      // gsap.to(controls.target, {
      //   x: position.x,
      //   y: position.y,
      //   z: position.z,
      //   duration: 1,
      // });

      gsap.to(camera.position, {
        x: eventList[i].position.x / 5,
        z: eventList[i].position.y / 5 + 8,
        duration: 1,
      });
    });
  }, []);

  return <div className="scene" ref={sceneDiv}></div>;
}
