import * as THREE from "three";
import gsap from "gsap";

export default class FlyLine {
  // 不使用着色器
  constructor(position = { x: 0, z: 0 }) {
    let linePoints = [new THREE.Vector3(0, 0, 0), new THREE.Vector3(position.x / 2, 4, position.z / 2), new THREE.Vector3(position.x, 0, position.z)];

    // 创建曲线
    this.lineCurve = new THREE.CatmullRomCurve3(linePoints);
    // 根据曲线生成管道几何体
    this.geometry = new THREE.TubeGeometry(this.lineCurve, 100, 0.5, 2, false);
    const textloader = new THREE.TextureLoader();
    this.texture = textloader.load("./textures/z_11.png");
    this.texture.repeat.set(1, 2);
    this.texture.wrapS = THREE.RepeatWrapping;
    this.texture.wrapT = THREE.MirroredRepeatWrapping;
    //创建材质
    this.material = new THREE.MeshBasicMaterial({
      // color: new THREE.Color(0xfff000),
      map: this.texture,
      transparent: true,
    });

    // 创建物体
    this.mesh = new THREE.Mesh(this.geometry, this.material);

    gsap.to(this.texture.offset, {
      x: -1,
      duration: 1,
      repeat: -1,
      ease: "none",
    });
  }
  remove() {
    this.mesh.remove();
    this.mesh.removeFromParent();
    this.mesh.geometry.dispose();
    this.mesh.material.dispose();
  }
}
