import React, { useEffect, useState, useRef } from "react";
import CountUp from "react-countup";
import eventHub from "../utils/eventHub";

export default function BigScreen(props) {
  const { leftData, eventList, imgs } = props;
  const [currentActive, setCurrentActive] = useState(null);
  useEffect(() => {
    eventHub.on("spriteClick", (data) => {
      setCurrentActive(() => {
        return data.i;
      });
    });
  }, []);

  const toggleEvent = (i) => {
    eventHub.emit("eventToggle", i);
    setCurrentActive(() => {
      return i;
    });
  };
  return (
    <div id="bigScreen">
      <div className="header">智慧城市管理平台</div>
      <div className="main">
        <div className="left">
          {leftData.map((item, index) => (
            <div className="cityEvent" key={index}>
              <h3>
                <span>{item.name}</span>
              </h3>
              <h1>
                <img src="./assets/bg/bar.svg" className="icon" />
                <span>
                  <CountUp end={item.number} duration={3} redraw={true} />
                  {item.unit}
                </span>
              </h1>
              <div className="footerBorder"></div>
            </div>
          ))}
        </div>

        <div className="right">
          {eventList.map((item, index) => {
            const styles = currentActive === index ? "active" : "";
            return (
              <div className="cityEvent list" key={index}>
                <h3>
                  <span>事件列表</span>
                </h3>
                <ul>
                  <li className={styles} onClick={() => toggleEvent(index)}>
                    <h1>
                      <div>
                        <img className="icon" src={imgs[`${item.name}`]} />
                        <span> {item.name} </span>
                      </div>
                      <span className="time"> {item.date} </span>
                    </h1>
                    <p style={{ fontSize: "14px" }}>{item.description}</p>
                  </li>
                </ul>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
