import gsap from "gsap";
import * as THREE from "three";
export default function modifyCityMaterial(mesh) {
  mesh.material.onBeforeCompile = (shader) => {
    // console.log(shader.vertexShader);
    // console.log(shader.fragmentShader);
    shader.fragmentShader = shader.fragmentShader.replace(
      "#include <dithering_fragment>",
      `
      #include <dithering_fragment>
      //#end#
      `
    );
    addGradColor(shader, mesh);
    addSpread(shader);
    addLightLine(shader);
    addToTopLine(shader);
  };
}

export function addGradColor(shader, mesh) {
  //获取最大点和最小点
  mesh.geometry.computeBoundingBox();
  const { min, max } = mesh.geometry.boundingBox;
  //获取高度差
  let uHeight = max.y - min.y;

  // 顶部颜色
  shader.uniforms.uTopColor = {
    value: new THREE.Color("#aaaeff"),
  };
  shader.uniforms.uHeight = {
    value: uHeight,
  };

  // 顶点着色器传递模型坐标给片元坐标系
  shader.vertexShader = shader.vertexShader.replace(
    "#include <common>",
    `
    #include <common>
    varying vec3 vPosition;
    `
  );
  // 在顶点着色器的主函数中为将要传递给片元着色器的varying变量赋值
  // position 是threejs底层定义好的模型坐标
  shader.vertexShader = shader.vertexShader.replace(
    "#include <fog_vertex>",
    `
    #include <fog_vertex>
    vPosition = position;
`
  );

  // 片元着色器接收： 1 外部传入的uniform变量；2 来自顶点着色器的varying变量
  shader.fragmentShader = shader.fragmentShader.replace(
    "#include <common>",
    `
    #include <common>
    uniform vec3 uTopColor;
    uniform float uHeight;
    varying vec3 vPosition;
  `
  );
  // 在片元着色器的主函数中
  shader.fragmentShader = shader.fragmentShader.replace(
    "//#end#",
    `
    vec4 distGradColor = gl_FragColor;
    // 设置渐变色比例
    float gradMix = (vPosition.y+uHeight/2.0)/uHeight;
    // 设置渐变效果 mix(a,b,r) = (1-r)*a + br
    vec3 gradMixColor = mix(distGradColor.xyz,uTopColor,gradMix);
    // 片元赋色
    gl_FragColor = vec4(gradMixColor,1);
    //#end#
    `
  );
}

export function addSpread(shader) {
  // 设置扩散的中心点
  shader.uniforms.uSpreadCenter = { value: new THREE.Vector2(0, 0) };
  // 扩散的时间
  shader.uniforms.uSpreadTime = { value: -1000 };
  // 设置条带的宽度
  shader.uniforms.uSpreadWidth = { value: 40 };
  // 设置半径
  // shader.uniforms.R = { value: 0 };
  // main函数外接收外部变量
  shader.fragmentShader = shader.fragmentShader.replace(
    "#include <common>",
    `
      #include <common>
      uniform vec2 uSpreadCenter;
      uniform float uSpreadTime;
      uniform float uSpreadWidth;
    `
  );
  // 圆环扩散
  shader.fragmentShader = shader.fragmentShader.replace(
    "//#end#",
    `
    float spreadRadius = distance(vPosition.xz,uSpreadCenter);
    //  扩散范围的函数
    float spreadIndex = -(spreadRadius-uSpreadTime) * (spreadRadius-uSpreadTime) + uSpreadWidth;
    if(spreadIndex > 0.0) {
        gl_FragColor = mix(gl_FragColor,vec4(1,1,1,1),spreadIndex/uSpreadWidth);
    }
    //#end#
    `
  );
  gsap.to(shader.uniforms.uSpreadTime, {
    value: 1000, // 从初始值到该值为界限范围
    duration: 2, // 一个周期
    ease: "power1.out", // 扩散速率函数
    repeat: -1, // 不断重复这个效果
  });
}

export function addLightLine(shader) {
  // 扩散的时间
  shader.uniforms.uLightLineTime = { value: -1000 };
  // 设置条带的宽度
  shader.uniforms.uLightLineWidth = { value: 400 };
  // 设置半径
  // shader.uniforms.R = { value: 0 };
  // main函数外接收外部变量
  shader.fragmentShader = shader.fragmentShader.replace(
    "#include <common>",
    `
      #include <common>
      uniform float uLightLineTime;
      uniform float uLightLineWidth;
    `
  );

  shader.fragmentShader = shader.fragmentShader.replace(
    "//#end#",
    `
   
    float LightLineMix = -(vPosition.x+vPosition.z-uLightLineTime) * (vPosition.x+vPosition.z-uLightLineTime) + uLightLineWidth;
    if(LightLineMix > 0.0) {
        gl_FragColor = mix(gl_FragColor,vec4(1,0.8,0.8,1),LightLineMix/uLightLineWidth);
    }
    //#end#
    `
  );
  gsap.to(shader.uniforms.uLightLineTime, {
    value: 1000, // 从初始值到该值为界限范围
    duration: 2, // 一个周期
    ease: "power1.out", // 扩散速率函数
    repeat: -1, // 不断重复这个效果
  });
}

export function addToTopLine(shader) {
  // 扩散的时间
  shader.uniforms.uToTopLineTime = { value: 0 };
  // 设置条带的宽度
  shader.uniforms.uToTopLineWidth = { value: 40 };
  // 设置半径
  // shader.uniforms.R = { value: 0 };
  // main函数外接收外部变量
  shader.fragmentShader = shader.fragmentShader.replace(
    "#include <common>",
    `
      #include <common>
      uniform float uToTopLineTime;
      uniform float uToTopLineWidth;
    `
  );

  shader.fragmentShader = shader.fragmentShader.replace(
    "//#end#",
    `
   
    float ToTopLineMix = -(vPosition.y-uToTopLineTime) * (vPosition.y-uToTopLineTime) + uToTopLineWidth;
    if(ToTopLineMix > 0.0) {
        gl_FragColor = mix(gl_FragColor,vec4(0.8,0.8,1,1),ToTopLineMix/uToTopLineWidth);
    }
    //#end#
    `
  );
  gsap.to(shader.uniforms.uToTopLineTime, {
    value: 500, // 从初始值到该值为界限范围
    duration: 2, // 一个周期
    ease: "power1.out", // 扩散速率函数
    repeat: -1, // 不断重复这个效果
  });
}
