import React, { useEffect, useRef, useState } from "react";
import BigScreen from "../components/BigScreen";
import Scene from "../components/Scene";

export default function Home() {
  const [first, setfirst] = useState(0);
  const leftData = useRef([
    {
      name: "接入IOT设备",
      number: 6600,
      unit: "台",
    },
    {
      name: "今日采集量",
      number: 2000,
      unit: "人",
    },
    {
      name: "城市电力能耗",
      number: 1000,
      unit: "兆瓦时",
    },
    {
      name: "未处理治安事件",
      number: 20,
      unit: "起",
    },
  ]);
  const imgs = {
    电力: process.env.PUBLIC_URL + "/assets/bg/dianli.svg",
    火警: process.env.PUBLIC_URL + "/assets/bg/fire.svg",
    治安: process.env.PUBLIC_URL + "/assets/bg/jingcha.svg",
  };
  const eventList = useRef([
    {
      name: "治安",
      date: "14:24:42",
      description: "存在安全隐患，需要派人排除",
      position: { x: 50, y: 56 },
    },
    {
      name: "火警",
      date: "14:24:42",
      description: "出现事故，需紧急处理",
      position: { x: 37, y: 46 },
    },
    {
      name: "电力",
      date: "14:24:42",
      description: "重大事故，需派人支援",
      position: { x: 40, y: 23 },
    },
    {
      name: "电力",
      date: "14:24:42",
      description: "重大事故，需派人支援",
      position: { x: 20, y: 23 },
    },
  ]);
  useEffect(() => {
    let task;
    task = setInterval(() => {
      for (let index = 0; index < leftData.current.length; index++) {
        leftData.current[index].number += 1;
        eventList.current[index].position.x = eventList.current[index].position.x + -Math.random() * 5;
        eventList.current[index].position.y = eventList.current[index].position.y + -Math.random() * 5;
      }
      setfirst((first) => first + 1);
    }, 5000);
    return () => {
      task && clearInterval(task);
    };
  }, []);
  return (
    <div>
      <Scene eventList={eventList.current}></Scene>
      <BigScreen leftData={leftData.current} imgs={imgs} eventList={eventList.current}></BigScreen>
    </div>
  );
}
